import axios from 'axios'
import router from '@/router/index'
import qs from 'query-string'

import { Loading, Message, Notification } from 'element-ui'

axios.defaults.withCredentials = true;

const instance = axios.create({
    headers: {'Content-Type': 'application/x-www-form-urlencoded'},
    //baseURL: 'http://testh5.huaqiaobao.cn',
    timeout: 300000,
    responseType: 'json',
    // withCredentials: true,  //cookies
});

instance.interceptors.request.use(config => {
    //
    console.log(qs);
    return config
  }, error => {
      return Promise.reject(error)
}) 

const HOST = process.env.NODE_ENV === 'development' ? '/api' : ''


//async await promisejs
const GetAjax = async function(url = '', params = {}, type = 'GET') {
    try {
        return await instance({
            url: HOST + url,
            method: type,
            // params: params,       paramsreq.bodyreq.querydataqs
            data: qs.stringify(params)
        })
    }
    catch(err) {
        console.log(err)
    }
}


export default GetAjax



